import React from 'react'
import {Container,Button} from './styles.js'

import {Link} from 'react-scroll'
export default function index() {
    return (
     <Container>
         
           <Link to="songs" smooth={true} duration={1000}><Button><p>Musicas Principais</p></Button></Link>
           <Link to="projects" smooth={true} duration={1000}> <Button><p>Projetos Principais</p></Button></Link>
           <Link to="others" smooth={true} duration={1000}><Button><p>Outros Projetos</p></Button></Link>
           <Link to="about" smooth={true} duration={1000}> <Button><p>Sobre Mim</p></Button></Link>
           <Link to="contato" smooth={true} duration={1000}> <Button><p>Contato</p></Button></Link>
    </Container>
    )
}
