import styled ,{css}from 'styled-components';
 

export const Container = styled.div`
width: 0;
height: 0;
border-style: solid;
border-width: 70vh 98.7vw 0 0;
border-color: #350455 transparent transparent transparent;
background:transparent;
display:inline-block;


justify-content:center;
position:relative;
@media(min-width:1440px){
  border-width: 70vh 94.7vw 0 0;

}

@media(min-width:1920px){
  border-width: 70vh 71vw 0 0;

}


`;

export const Master = styled.div`
 position:relative;
text-align:center;
>h1{
  margin-top:12px;
  color:white;
  position:absolute;
  font-size:55px;
  padding:20px;
  font-family: 'Montserrat', sans-serif;
  top:0;
}

`;

export const Content = styled.div`

  position:absolute;
  top:150px;
  right:1000px;
  

>p{
  color:black;
  font-size:20px;
  margin-top:20px;
}

@media(min-width:1440px){
    top:300px;

}
`;

export const Song = styled.div`


  
   background:gray;
   border:2px solid black;
 
   left:100px;
  
`;

export const Rectangle = styled.div`

    width:100%;
    height:30vh;
    background:#350455;

    @media(min-width:1440px){
     height:20vh;

}

@media(min-width:1920px){
     height:15vh;

}
`;

export const Listras = styled.div`
    position:absolute;
    display:flex;
    flex-direction:row;

    bottom:0;
    right:0;

`;

const listraCss = css`

   height:100vh;
   width:30px;
   background:#350455;
   margin-right:20px;
   @media(max-width:1240px){


display:none;

}

`;
export const Iframe= styled.div`
 font-size: 10px;
  color: #cccccc;
  line-break:auto;
  word-break: normal;
  overflow: hidden;
  white-space: nowrap;
  
  text-overflow: ellipsis;
  
   font-weight: 100;




 
`;

export const IframeStyle = styled.div`

        border:0;
        width:820;
        height:480;

`;

export const Listra1 = styled.div`${listraCss}`;
export const Listra2 = styled.div`${listraCss}`;