import styled ,{css}from 'styled-components';

export const Container = styled.div`
    
    height:100vh;
    width:100%;
   
    margin-top:100px;
    @media(max-width:1000px){
         

         margin-bottom:10px;
}
    
`;

export const Contents = styled.div`
  
    
  @media(max-width:1000px){
         
             text-align:center;

     
}


`;

export const SectionName = styled.div`
  
 background:#350455;
 width:50%;

>h1{
  color:white;

  font-size:55px;
  padding:20px;
  font-family: 'Montserrat', sans-serif;
  top:0;
}

@media(max-width:1240px){

>h1{

  font-size:40px;
 
  
}

}

@media(max-width:900px){

>h1{

  font-size:30px;
 
  
}

}

@media(max-width:650px){
   h1{

font-size:20px;

}

 }

`;
export const Content1 = styled.div`

  display:flex;
  flex-direction:row;
  justify-content:start;
  padding:20px;


   @media(max-width:900px){
         

          display:flex;
          flex-direction:column;
}
  

`;
export const Video = styled.div`
   
   height:250px;
   width:450px;
   background:gray;
   border:2px solid black;

   
  




`;
export const Descricao = styled.div`

 
   margin-left:100px;
   width:40%;
   
    >h1{
        color:black;
        font-family: 'Montserrat', sans-serif;
    }
   >p{
       margin-top:24px;
       color:black;
       font-family: 'Montserrat', sans-serif;
   }

   @media(max-width:1240px){

          >p{

            font-size:13px;
            width:400px;
            
          }

   }
          
   @media(max-width:1090px){

    >p{

      font-size:13px;
      width:300px;
      text-align:start;
      
      
    }

  

}
@media(max-width:900px){

>p,h1{

  display:none;
  
}

 }
 
 @media(max-width:800px){

>p,h1{

  margin-left:100px;
  
}
>h1{

font-size:16px;
}
>p{
  font-size:11px;
}


 }
 @media(max-width:650px){
  >p,h1{

margin-left:1px;

}

 }
 @media(max-width:530px){
  >p,h1{

margin-left:-50px;

}

 }

`;

export const Content2 = styled.div`

  display:flex;
  flex-direction:row;
  justify-content:center;
  padding:20px;
  
  @media(max-width:900px){
         

         display:flex;
         flex-direction:column-reverse;
         margin-bottom:200px;
}
 
  

`;
export const Video2 = styled.div`
   
   height:250px;
   width:450px;
   background:gray;
   border:2px solid black;
   margin-right:20px;

`;
export const Descricao2 = styled.div`


   margin-right:100px;
   width:40%;
    

   >h1{
       color:black;
       font-family: 'Montserrat', sans-serif;
   }

   >p{
       margin-top:24px;
       color:black;
       font-family: 'Montserrat', sans-serif;
   }

   @media(max-width:1240px){

>p{

  font-size:13px;
  width:400px;
  
}



@media(max-width:1090px){

>p{

font-size:13px;
width:300px;

}

}

@media(max-width:900px){

>p,h1{

  display:none;
  
}

 }
 }

 @media(max-width:800px){

>p,h1{

  margin-left:200px;
  
}
>h1{

font-size:16px;
}


 }

 @media(max-width:650px){
  >p,h1{

margin-left:100px;

}

 }

 @media(max-width:530px){

  >p,h1{

margin-left:20px;

}

 }




`;

export const Listras = styled.div`
    position:absolute;
    display:flex;
    flex-direction:row;

    bottom:0;
    right:0;

`;

const listraCss = css`

   height:1px;
   width:30px;
   background:#350455;
   margin-right:20px;
   
   background:transparent;

   @media(max-width:1240px){


display:none;
  
   }

`;

export const Listra1 = styled.div`${listraCss}`;
export const Listra2 = styled.div`${listraCss}`;