import React,{useState} from 'react'
import {Container,SectionName,Formulario,Icons,Botao,Info,Infos,SpotifyIcon,YoutubeIcon,
        TwitterIcon,InstagramIcon,LinkedinIcon,Listras,Listra1,Listra2} from './styles.js'
export default function Index() {

    const [name,setName] = useState('')

     const validator = (event) =>{
         
        if(event.target.length <3){

            alert("Por favor, indique o seu nome.");
            event.target.value.focus();
        }

     }

     const changeText =(event)=>{

        setName(event.target.value)
 
     }

    return (
        <section id="contato">
        <Container>
            <SectionName>
                <h1>Contato</h1>
            </SectionName>

            <Formulario>
               
                            

                    <fieldset class="grupo">
                    
                            <div class="campo">
                          
                                <input type="text" name="nome" id="nome" value={name} onChange={changeText} size="100" required placeholder="Seu nome"/>
                                
                            </div>
                            
                        
                            <div class="campo">
                              
                                <input type="email" name="email"  id="emailid" required size="100" placeholder="Seu email"/>
                            </div>
                            <div class="campo">
                             
                                <textarea name="" id="" cols="103" rows="7" placeholder="Sua mensagem" ></textarea>
                            </div>

                        </fieldset>  
                <Botao onClick={validator}>
                     <p>Enviar</p>
                    
                 </Botao>
                 </Formulario>

                 
         
                 <Info>
                     <Infos>
                         <h2>Bitdreamer Sound Design</h2>
                         <p>bitdreamersd@gmail.com</p>
                     </Infos>
                     <Icons>
                          <TwitterIcon/>
                          <InstagramIcon/>
                          <LinkedinIcon/>
                          <SpotifyIcon/>
                          <YoutubeIcon/>
                     </Icons>
                 </Info>

                 <Listras>
                    <Listra1/>
                    <Listra2/>
                </Listras>
        </Container>

        </section>
    )


    
}
