import React from 'react'
import {Container,First,Second,Photo,Description,Content,SectionName,Listras,Listra1,Listra2} from './styles.js'
export default function index() {
    return (

        <section id="about">
        <Container>
     
           <First/>
           <SectionName>
                <h1><u>Sobre Mim</u></h1>
            </SectionName>
            <Content>
  
                    <Photo/>
                <Description>

                <p>Me chamo Wesley Ferreira. Estou ativo na área de games desde 2018, onde comecei a produzir 
                    áudio para os primeiros trabalhos da faculdade de Game Design.
                         Desde então segui evoluindo e estudando de forma aprofundada toda a produção e
                          implementação de áudio in-game, onde entrei para a Game Audio Academy em 2019 e fui
                            um dos destaques da minha turma.</p>
                            <p> Após a formação, segui aprimorando e atualmente me
                             encontro aberto a oportunidades de desenvolvimento e integração de música,
                        efeitos sonoros e sistemas de áudio dinâmico para games.</p>
                </Description>

            </Content>
            <Listras>
                    <Listra1/>
                    <Listra2/>
                </Listras>
          
        </Container>
        </section>
    )
}
