import React from 'react'
import Yt from 'react-youtube'
import './projects.css'
import {Container,SectionName,Content1,Video,Descricao,Content2,
         Video2,Descricao2,Contents,Listras,Listra1,Listra2} from './styles.js'
export default function index() {
    return (
        <section id="projects">
            <Container>
                <SectionName>
                    <h1>Projetos Principais</h1>
                </SectionName>
                <Contents>
                    <Content1>
                        <Yt className="YtContainer" videoId="0lG2RDkBkFA"></Yt>
                        <Descricao>
                            <h1>CORUJAS</h1>
                            <p>Corujas é um jogo em terceira pessoa, de aventura e exploração, focado em acontecimentos históricos
                                 na região de Rondônia, retratando com um olhar único a história da Estrada de Ferro Madeira Mamoré (ferrovia do diabo).
  
                               Tutu, um jornalista, deverá investigar sobre o que aconteceu no local e a causa de tantas mortes que até agora é um mistério.

                                   Disponível na itch.io: https://sunrisegames.itch.io/corujas</p>
                        </Descricao>
                    </Content1>
                    <Content2>
                    
                        <Descricao2>
                            <h1>BANG DUEL </h1>
                            <p>Bang Duel é um jogo estilo arcade, de confronto 1x1 ou 1xCPU. O jogador deve ser rápido o bastante para fazer o disparo antes
                                            de seu oponente.

                                        Disponível na Google Play.</p>
                        </Descricao2>
                        <Yt className="YtContainer" videoId="DMZuIAP3NbY"></Yt>
                    </Content2>
                    
                </Contents>
                <Listras>
                        <Listra1/>
                        <Listra2/>
                </Listras>
            </Container>
            
        </section>
    )
}
