import styled,{css} from 'styled-components';

export const Container = styled.div`
    
    height:100vh;
    width:100%;
    position:relative;
    margin-top:50px;
    margin-bottom:300px;
  
    @media(max-width:1000px){
         

         margin-bottom:350px;
}
    
`;


export const Contents = styled.div`
  
    
  @media(max-width:1000px){
         
         text-align:center;

 
}

`;


export const Content1 = styled.div`

  display:flex;
  flex-direction:row;
  justify-content:start;
  padding:20px;
  @media(max-width:900px){
         

         display:flex;
         flex-direction:column;
}

`;
export const Video = styled.div`
   
   height:250px;
   width:450px;
   background:gray;
   border:2px solid black;

`;
export const Descricao = styled.div`

 
   margin-left:100px;
   width:40%;
   
    >h1{
        color:black;
        font-family: 'Montserrat', sans-serif;
    }
   >p{
       margin-top:24px;
       color:black;
       font-family: 'Montserrat', sans-serif;
   }

   @media(max-width:1240px){

>p{

  font-size:13px;
  width:400px;
  
}

}

@media(max-width:1090px){

>p{

font-size:13px;
width:300px;
text-align:start;

}



}
@media(max-width:px){

>p,h1{

display:none;

}


}




`;

export const Content2 = styled.div`

margin-top:40px;
width:70%;
margin-left:200px;
text-align:center;
margin-bottom:100px;



>h1{

 margin-bottom:10px;
 font-family: 'Montserrat', sans-serif;

}
 
 
  

`;
export const Video2 = styled.div`
   
   height:250px;
   width:450px;
   background:gray;
   border:2px solid black;

`;
export const Descricao2 = styled.div`



   width:40%;
   margin-right:100px; 

   >h1{
       color:black;
   }

   >p{
       margin-top:24px;
       color:black;
       
   }

   

`;

export const SectionName= styled.div`


 background:#350455;
 width:25%;

>h1{
  color:white;
   text-align:center; 
   border-bottom:1px solid white;
  font-size:55px;
  padding:20px;
  font-family: 'Montserrat', sans-serif;
  bottom:0;
  right:0;
}
`;


export const Listras = styled.div`
    position:absolute;
    display:flex;
    flex-direction:row;

    bottom:0;
    right:0;

`;

const listraCss = css`

  height:230vh;
   width:30px;
   background:#350455;
   margin-right:20px;

   @media(max-width:1240px){


    display:none;
  
   }


`;

export const Listra1 = styled.div`${listraCss}`;
export const Listra2 = styled.div`${listraCss}`;